<template>
  <el-card class="table-card">
    <div class="header" slot="header">
      <span>路由管理</span>
    </div>
    <el-table :data="allRoutes" row-key="permission_id" border default-expand-all>
      <el-table-column prop="route_name" label="路由名称" width="180">
      </el-table-column>
      <el-table-column prop="route_id" label="#">
      </el-table-column>
      <el-table-column prop="route_title" label="显示标题">
      </el-table-column>
      <el-table-column prop="route_path" label="路由地址" width="120">
      </el-table-column>
      <el-table-column prop="route_full_path" label="完整路由地址" width="180">
      </el-table-column>
      <el-table-column prop="component_name" label="组件名称">
      </el-table-column>
      <el-table-column prop="component_path" label="组件路径" width="200">
      </el-table-column>
      <el-table-column prop="route_alias" label="路由别名">
      </el-table-column>
      <el-table-column prop="permission_remark" label="备注">
      </el-table-column>
      <el-table-column prop="require_auth" label="登录状态认证">
      </el-table-column>
      <el-table-column label="操作" width="280" fixed="right">
        <template slot-scope="scope">
          <el-button @click="openEditDialog(scope.row)" :disabled="scope.row.permission_id === 0" icon="el-icon-edit"
                     type="primary" plain>
            编辑
          </el-button>
          <el-button @click="openInserDialog(scope.row.permission_id, scope.row)" icon="el-icon-circle-plus-outline"
                     type="success" plain>
            添加
          </el-button>
          <el-button @click="handleRemoveRoute(scope.row)" :disabled="scope.row.permission_id === 0"
                     icon="el-icon-delete" type="danger" plain>
            删除
          </el-button>
        </template>
      </el-table-column>
    </el-table>
    <!-- 添加路由Dialog -->
    <el-dialog title="添加路由" :visible.sync="insert_dialog_visible" @close="handleDialogClose('insert_form')">
      <el-form ref="insert_form" :rules="rules" :model="insert_form" label-width="150px">
        <el-form-item label="路由显示标题" prop="route_title">
          <el-input v-model="insert_form.route_title" placeholder="路由成为链接时显示的标题文字"></el-input>
        </el-form-item>
        <el-form-item label="路由名称" prop="route_name">
          <el-input v-model="insert_form.route_name" placeholder="以大驼峰命名格式填写英文单词"></el-input>
        </el-form-item>
        <el-form-item label="路由地址" prop="route_path">
          <el-input v-model="insert_form.route_path"
                    placeholder="一级路由：以‘/route_path’格式填写，子级路由：以‘route_path’格式填写"></el-input>
        </el-form-item>
        <el-form-item label="父级路由权限ID" prop="parent_id">
          <el-input v-model="insert_form.parent_id" readonly></el-input>
        </el-form-item>
        <el-form-item label="路由完整地址" prop="route_full_path">
          <el-input v-model="insert_form.route_full_path" placeholder="父级路由地址 + 自身地址叠加形成"></el-input>
        </el-form-item>
        <el-form-item label="对应组件名称" prop="component_name">
          <el-input v-model="insert_form.component_name" placeholder="组件名称，以大驼峰命名格式填写英文单词"></el-input>
        </el-form-item>
        <el-form-item label="组件相对路径" prop="component_path">
          <el-input v-model="insert_form.component_path" placeholder="vue文件的相对路径"></el-input>
        </el-form-item>
        <el-form-item label="路由别名" prop="route_alias">
          <el-input v-model="insert_form.route_alias"></el-input>
        </el-form-item>
        <el-form-item label="是否认证登录状态" prop="require_auth">
          <el-radio-group v-model="insert_form.require_auth">
            <el-radio :label="1">需要认证</el-radio>
            <el-radio :label="0">对外公开</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="备注信息" prop="permission_remark">
          <el-input v-model="insert_form.permission_remark" placeholder="备注信息，同时作为权限备注"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer">
        <el-button @click="insert_dialog_visible = false">取 消</el-button>
        <el-button @click="handleInsertRoute" type="primary">确 定</el-button>
      </div>
    </el-dialog>
    <!-- 编辑路由Dialog -->
    <el-dialog title="编辑路由" :visible.sync="edit_dialog_visible" @close="handleDialogClose('edit_form')">
      <el-form ref="edit_form" :rules="rules" :model="edit_form" label-width="150px">
        <el-form-item label="路由显示标题" prop="route_title">
          <el-input v-model="edit_form.route_title" placeholder="路由成为链接时显示的标题文字"></el-input>
        </el-form-item>
        <el-form-item label="路由名称" prop="route_name">
          <el-input v-model="edit_form.route_name" placeholder="以大驼峰命名格式填写英文单词"></el-input>
        </el-form-item>
        <el-form-item label="路由地址" prop="route_path">
          <el-input v-model="edit_form.route_path"
                    placeholder="一级路由：以‘/route_path’格式填写，子级路由：以‘route_path’格式填写"></el-input>
        </el-form-item>
        <el-form-item label="父级路由权限ID" prop="parent_id">
          <el-input v-model="edit_form.parent_id" readonly></el-input>
        </el-form-item>
        <el-form-item label="路由完整地址" prop="route_full_path">
          <el-input :value="route_full_path('edit_form')" placeholder="父级路由地址 + 自身地址叠加形成"></el-input>
        </el-form-item>
        <el-form-item label="对应组件名称" prop="component_name">
          <el-input v-model="edit_form.component_name" placeholder="组件名称，以大驼峰命名格式填写英文单词"></el-input>
        </el-form-item>
        <el-form-item label="组件相对路径" prop="component_path">
          <el-input v-model="edit_form.component_path" placeholder="vue文件的相对路径"></el-input>
        </el-form-item>
        <el-form-item label="路由别名" prop="route_alias">
          <el-input v-model="edit_form.route_alias"></el-input>
        </el-form-item>
        <el-form-item label="是否认证登录状态" prop="require_auth">
          <el-radio-group v-model="edit_form.require_auth">
            <el-radio :label="1">需要认证</el-radio>
            <el-radio :label="0">对外公开</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="备注信息" prop="permission_remark">
          <el-input v-model="edit_form.permission_remark" placeholder="备注信息，同时作为权限备注"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer">
        <el-button @click="edit_dialog_visible = false">取 消</el-button>
        <el-button @click="handleEditRoute" type="primary">保 存</el-button>
      </div>
    </el-dialog>
  </el-card>
</template>

<script>
import { mapGetters, mapActions } from 'vuex';

export default {
  name: "Route",
  data() {
    return {
      insert_dialog_visible: false,
      edit_dialog_visible: false,
      insert_form: {
        route_title: '',
        route_name: '',
        route_path: '',
        parent_id: '',
        route_full_path: '',
        component_name: '',
        component_path: '',
        route_alias: '',
        require_auth: 1,
        permission_remark: '',
      },
      edit_form: {
        route_title: '',
        route_name: '',
        route_path: '',
        parent_id: '',
        route_full_path: '',
        component_name: '',
        component_path: '',
        route_alias: '',
        require_auth: 1,
        permission_remark: '',
      },
      //当前操作行route的指针
      current_route: '',
      rules: {
        route_title: [{ required: true, message: "请输入路由成为链接时显示的标题文字", trigger: 'blur' }],
        route_name: [{ required: true, message: "请输入路由名称，以大驼峰命名格式填写英文单词", trigger: 'blur' }],
        route_path: [{ required: true, message: "请输入路由自身地址，一级路由：以‘/route_path’格式填写，子级路由：以‘route_path’格式填写", trigger: 'blur' }],
        parent_id: [{ required: true, message: "请输入父级路由权限ID", trigger: 'blur' }],
        route_full_path: [{ required: true, message: "请输入路由完整地址，父级路由地址 + 自身地址", trigger: 'blur' }],
        component_name: [{ required: true, message: "请输入路由对应组件名称", trigger: 'blur' }],
        component_path: [{ required: true, message: "请输入路由vue文件的相对路径", trigger: 'blur' }],
        route_alias: [{ required: false, message: "请输入路由别名", trigger: 'blur' }],
        require_auth: [{ required: true, message: "请选择是否认证登录状态", trigger: 'change' }],
        permission_remark: [{ required: true, message: "请输入备注信息", trigger: 'blur' }],
      },
    }
  },
  created() {
    document.title = "路由管理";
    // 加载路由列表
    this.loadRouteList({ type: 'flat' });
  },
  computed: {
    ...mapGetters('Route', ['allRoutes']),
    route_full_path() {
      // TODO 修改为watch监听
      return (form) => {
        let full_path;
        let current_full_path = this.current_route?.route_full_path;
        // 添加子路由
        if (form === 'insert_form') {
          let child_path = this.insert_form.route_path;
          full_path = `${current_full_path}/${child_path}`;
        }
        // 编辑路由
        if (form === 'edit_form' && this.current_route) {
          let child_path = this.edit_form.route_path;
          // 当前路由是二级路由
          if (this.edit_form.parent_id) {
            // 拆分为数组
            let arr = current_full_path.split('/');
            // 去除 child_path
            arr.pop();
            // 计算出 parent_path
            let parent_path = arr.join('/');
            full_path = `${parent_path}/${child_path}`;
          }
          // 当前路由是一级路由
          if (this.edit_form.parent_id === 0) {
            full_path = child_path;
          }
        }
        this[form].route_full_path = full_path;
        return full_path;
      }
    },
  },
  watch: {
    "insert_form.route_path"(new_path) {
      let full_path;
      let { route_full_path: parent_path, permission_id: parent_id } = this.current_route;
      // 如果添加一级路由，parent_id === 0
      if (parent_id === 0) {
        full_path = new_path;
      }
      // 如果添加二级以下路由
      if (parent_id && parent_path) {
        full_path = `${parent_path}/${new_path}`;
      }

      this.insert_form.route_full_path = full_path;
    },
  },
  methods: {
    // 加载所有 action
    ...mapActions('Route', ['loadRouteList', 'createRoute', 'editRoute', 'removeRoute']),
    // 打开添加弹窗
    openInserDialog(parent_id, route) {
      // 缓存父级id
      this.insert_form.parent_id = parent_id;
      // 缓存当前操作行的指针
      this.current_route = route;
      // 打开弹窗
      this.insert_dialog_visible = true;
    },
    // 添加路由
    handleInsertRoute() {
      this.$refs.insert_form.validate(async (valid) => {
        if (valid) {
          let { status, msg } = await this.createRoute({ ...this.insert_form });
          if (status) {
            // 关闭弹窗
            this.insert_dialog_visible = false;
            // 信息提示
            this.$message.success(msg);
          } else {
            this.$message.error(msg);
          }
        }
      });
    },
    // 打开编辑弹窗
    openEditDialog(route) {
      // 缓存当前操作行的指针
      this.current_route = route;
      // 浅拷贝
      this.edit_form = { ...route };
      this.edit_dialog_visible = true;
    },
    // 编辑路由Route
    handleEditRoute() {
      this.$refs.edit_form.validate(async (valid) => {
        if (valid) {
          let { status, msg } = await this.editRoute({ ...this.edit_form });
          if (status) {
            // 关闭弹窗
            this.edit_dialog_visible = false;
            // 消息提示
            this.$message.success(msg);
          } else {
            this.$message.error(msg);
          }
        }
      });
    },
    // 关闭弹窗
    handleDialogClose(form) {
      this.$refs[form].resetFields();
      this.current_route = null;
    },
    // 删除角色
    async handleRemoveRoute(route) {
      // 判断是否拥有子级路由
      if (route.children?.length) {
        this.$message.error("拥有子级路由，不允许删除！");
        return;
      }
      this.$confirm('此操作将永久删除该路由, 是否继续?', { type: 'warning' })
          .then(async () => {
            // 删除角色
            let { status, msg } = await this.removeRoute(route);
            if (status) {
              // 消息提示
              this.$message.success(msg);
            } else {
              this.$message.error(msg);
            }
          })
          .catch(() => {
            this.$message.info('取消成功！');
          });
    },
  }
}
</script>

<style lang="less" scoped></style>